"use client";

import { useState } from "react";
import Link from "next/link";
import { motion, AnimatePresence } from "framer-motion";
import { Product, Language } from "@/lib/types";
import { Sparkles, Search, ChevronRight, ArrowRight, Truck, ShieldCheck, Phone } from "lucide-react";

const LANG_FLAGS: Record<Language, string> = { en: "üá¨üáß", fr: "üá´üá∑", tr: "üáπüá∑" };

export function ProductDetailClient({ product }: { product: Product }) {
    const [lang, setLang] = useState<Language>("fr");
    const [selectedImage, setSelectedImage] = useState(product.images[0] || "");
    const [selectedVariant, setSelectedVariant] = useState(0);

    const currentVariant = product.variants[selectedVariant];
    const hasPromo = currentVariant?.promoPrice && currentVariant.promoPrice < currentVariant.price;
    const displayPrice = hasPromo ? currentVariant.promoPrice : currentVariant?.price;

    return (
        <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ duration: 0.6 }}
            className="bg-background-light dark:bg-background-dark min-h-screen"
        >
            {/* Breadcrumb */}
            <nav className="w-full px-6 md:px-12 pt-6 pb-2">
                <div className="flex items-center gap-2 text-xs text-gray-400">
                    <Link href="/" className="hover:text-primary transition-colors">Accueil</Link>
                    <ChevronRight size={12} />
                    <Link href="/collection" className="hover:text-primary transition-colors">Collection</Link>
                    <ChevronRight size={12} />
                    <span className="text-gray-600 dark:text-gray-300 font-medium">{product.title}</span>
                </div>
            </nav>

            <section className="w-full px-6 md:px-12 py-8 md:py-16 grid grid-cols-1 lg:grid-cols-12 gap-12 lg:gap-20">

                {/* ‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê */}
                {/*  GALLERY                                    */}
                {/* ‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê */}
                <div className="lg:col-span-7 flex flex-col md:flex-row gap-5">
                    {/* Thumbnail Rail */}
                    <div className="order-2 md:order-1 flex md:flex-col gap-3 overflow-x-auto md:overflow-y-auto md:max-h-[700px] scrollbar-hide py-1">
                        {product.images.map((img, idx) => (
                            <button
                                key={idx}
                                onClick={() => setSelectedImage(img)}
                                className={`relative w-[72px] h-[72px] shrink-0 rounded-2xl overflow-hidden border-2 transition-all duration-300 p-1.5 bg-white dark:bg-gray-800/80
                                    ${selectedImage === img
                                        ? "border-primary ring-2 ring-primary/20 shadow-lg shadow-primary/5"
                                        : "border-gray-100 dark:border-gray-700/60 hover:border-gray-300 dark:hover:border-gray-600"
                                    }`}
                            >
                                <img src={img} alt={`${product.title} ${idx + 1}`} className="w-full h-full object-contain rounded-lg" />
                            </button>
                        ))}
                    </div>

                    {/* Main Image */}
                    <div className="order-1 md:order-2 flex-1 relative group">
                        <div className="aspect-square md:aspect-4/5 bg-gradient-to-br from-white via-gray-50 to-gray-100 dark:from-gray-900 dark:via-gray-800 dark:to-gray-900 rounded-3xl border border-gray-100 dark:border-gray-800 overflow-hidden flex items-center justify-center p-10 md:p-16">
                            <button className="absolute top-5 right-5 w-11 h-11 rounded-full bg-white/90 dark:bg-gray-800/90 backdrop-blur-xl flex items-center justify-center text-gray-400 hover:text-primary transition-all border border-gray-100 dark:border-gray-700 z-10 shadow-sm hover:shadow-md hover:scale-110">
                                <Search size={18} />
                            </button>

                            <AnimatePresence mode="wait">
                                {selectedImage ? (
                                    <motion.img
                                        key={selectedImage}
                                        initial={{ opacity: 0, scale: 0.92 }}
                                        animate={{ opacity: 1, scale: 1 }}
                                        exit={{ opacity: 0, scale: 1.05 }}
                                        transition={{ duration: 0.35, ease: [0.25, 0.46, 0.45, 0.94] }}
                                        alt={product.title}
                                        className="max-w-full max-h-full object-contain drop-shadow-2xl group-hover:scale-[1.03] transition-transform duration-700"
                                        src={selectedImage}
                                    />
                                ) : (
                                    <div className="text-gray-300 flex flex-col items-center gap-3">
                                        <Search size={36} strokeWidth={1} />
                                        <span className="text-sm font-medium">Pas d&apos;image</span>
                                    </div>
                                )}
                            </AnimatePresence>
                        </div>

                        {/* New / Promo Badge */}
                        {(product.isNew || hasPromo) && (
                            <div className="absolute top-4 left-4 flex flex-col gap-2 z-10">
                                {product.isNew && (
                                    <span className="bg-primary text-white text-[10px] font-bold uppercase tracking-widest px-4 py-1.5 rounded-full shadow-lg shadow-primary/30">
                                        Nouveau
                                    </span>
                                )}
                                {hasPromo && (
                                    <span className="bg-red-500 text-white text-[10px] font-bold uppercase tracking-widest px-4 py-1.5 rounded-full shadow-lg shadow-red-500/30">
                                        Promo
                                    </span>
                                )}
                            </div>
                        )}
                    </div>
                </div>

                {/* ‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê */}
                {/*  PRODUCT INFO                               */}
                {/* ‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê */}
                <div className="lg:col-span-5 lg:sticky lg:top-28 self-start">

                    {/* Language Switcher */}
                    <div className="flex gap-1.5 justify-end mb-8">
                        {(["en", "fr", "tr"] as Language[]).map((l) => (
                            <button
                                key={l}
                                onClick={() => setLang(l)}
                                className={`text-[10px] uppercase px-3 py-1.5 rounded-full border transition-all duration-200 font-semibold
                                    ${lang === l
                                        ? "bg-gray-900 text-white border-gray-900 dark:bg-white dark:text-gray-900 shadow-sm"
                                        : "text-gray-400 border-gray-200 dark:border-gray-700 hover:border-gray-400 hover:text-gray-600"
                                    }`}
                            >
                                {LANG_FLAGS[l]} {l}
                            </button>
                        ))}
                    </div>

                    {/* Title Block */}
                    <header className="space-y-3 mb-8">
                        <p className="text-[10px] uppercase tracking-[0.3em] text-primary font-bold">Exclusivit√©s</p>
                        <h1 className="text-4xl md:text-5xl font-display leading-[1.1] text-gray-900 dark:text-white">
                            {product.title}
                        </h1>
                        {product.subtitle && (
                            <p className="text-base font-light text-gray-400 dark:text-gray-500 tracking-widest uppercase">
                                {product.subtitle}
                            </p>
                        )}
                    </header>

                    {/* Price Display */}
                    {currentVariant && (
                        <div className="mb-8 flex items-end gap-4">
                            <span className="text-3xl md:text-4xl font-bold text-gray-900 dark:text-white leading-none">
                                ‚Ç¨ {displayPrice?.toLocaleString("fr-FR", { minimumFractionDigits: 2 })}
                            </span>
                            {hasPromo && (
                                <span className="text-lg line-through text-gray-400 font-light mb-0.5">
                                    ‚Ç¨ {currentVariant.price.toLocaleString("fr-FR", { minimumFractionDigits: 2 })}
                                </span>
                            )}
                        </div>
                    )}

                    {/* Description */}
                    <div className="text-gray-500 dark:text-gray-400 leading-[1.8] font-light text-[15px] whitespace-pre-line mb-10">
                        <p>{product.description[lang]}</p>
                    </div>

                    {/* ‚îÄ‚îÄ Features ‚îÄ‚îÄ */}
                    {product.features.length > 0 && (
                        <div className="grid grid-cols-2 sm:grid-cols-3 gap-8 py-10 border-y border-gray-100 dark:border-gray-800/50">
                            {product.features.map((f, i) => (
                                <div key={i} className="flex flex-col items-center text-center group">
                                    <div className="w-20 h-20 mb-6 flex items-center justify-center grayscale group-hover:grayscale-0 transition-all duration-700 opacity-80 group-hover:opacity-100 transform group-hover:scale-110">
                                        {f.image ? (
                                            <img src={f.image} alt={f.name} className="max-w-full max-h-full object-contain" />
                                        ) : (
                                            <Sparkles size={28} className="text-gray-300" />
                                        )}
                                    </div>
                                    <span className="text-sm md:text-base font-display text-gray-800 dark:text-gray-200 group-hover:text-primary transition-colors leading-tight">
                                        {f.name}
                                    </span>
                                </div>
                            ))}
                        </div>
                    )}

                    {/* ‚îÄ‚îÄ Dimensions ‚îÄ‚îÄ */}
                    {product.dimensions && (product.dimensions.totalHeight || product.dimensions.mattressHeight || product.dimensions.baseHeight) && (
                        <div className="mb-8">
                            <h3 className="text-[10px] uppercase tracking-[0.25em] font-bold mb-6 text-gray-400">
                                Sp√©cifications
                            </h3>
                            <div className="grid grid-cols-3 gap-4">
                                {(['totalHeight', 'mattressHeight', 'baseHeight'] as const).map((key) => {
                                    const val = product.dimensions?.[key];
                                    const img = product.dimensions?.[`${key}Image` as keyof typeof product.dimensions];
                                    const labels = { totalHeight: "Totale", mattressHeight: "Matelas", baseHeight: "Base" };
                                    if (!val) return null;
                                    return (
                                        <div key={key} className="bg-gray-50 dark:bg-gray-800/40 rounded-2xl p-5 text-center group hover:bg-primary/5 transition-colors duration-300">
                                            <div className="aspect-square rounded-xl bg-white dark:bg-gray-700/30 flex items-center justify-center mb-4 overflow-hidden">
                                                {img ? (
                                                    <img src={img as string} alt="" className="w-full h-full object-contain p-2 group-hover:scale-110 transition-transform duration-500" />
                                                ) : (
                                                    <div className="group-hover:scale-110 transition-transform duration-500">
                                                        {key === 'totalHeight' && (
                                                            <svg width="36" height="36" viewBox="0 0 100 100" fill="none" className="text-gray-300 group-hover:text-primary transition-colors">
                                                                <path d="M20 75L50 85L80 75L50 65L20 75Z" stroke="currentColor" strokeWidth="1.5" />
                                                                <path d="M20 75V60L50 70V85M80 75V60L50 70" stroke="currentColor" strokeWidth="1.5" />
                                                                <path d="M20 60L50 70L80 60L50 50L20 60Z" stroke="currentColor" strokeWidth="1.5" strokeLinejoin="round" />
                                                                <path d="M20 60V25H30V52" stroke="currentColor" strokeWidth="1.5" />
                                                                <path d="M12 25H8M12 85H8M10 25V85" stroke="currentColor" strokeWidth="1" strokeDasharray="2 2" />
                                                            </svg>
                                                        )}
                                                        {key === 'mattressHeight' && (
                                                            <svg width="36" height="36" viewBox="0 0 100 100" fill="none" className="text-gray-300 group-hover:text-primary transition-colors">
                                                                <path d="M20 75L50 85L80 75L50 65L20 75Z" stroke="currentColor" strokeWidth="1" opacity="0.3" />
                                                                <path d="M20 60L50 70L80 60L50 50L20 60Z" stroke="currentColor" strokeWidth="2" fill="currentColor" fillOpacity="0.1" />
                                                                <path d="M20 60V50L50 60V70L20 60Z" stroke="currentColor" strokeWidth="1.5" />
                                                                <path d="M50 50L80 40M20 50L50 60L80 50L50 40L20 50Z" stroke="currentColor" strokeWidth="1.5" />
                                                                <path d="M15 50H11M15 60H11M13 50V60" stroke="currentColor" strokeWidth="1" strokeDasharray="1 1" />
                                                            </svg>
                                                        )}
                                                        {key === 'baseHeight' && (
                                                            <svg width="36" height="36" viewBox="0 0 100 100" fill="none" className="text-gray-300 group-hover:text-primary transition-colors">
                                                                <path d="M20 60L50 70L80 60L50 50L20 60Z" stroke="currentColor" strokeWidth="1" opacity="0.2" />
                                                                <path d="M20 75L50 85L80 75L50 65L20 75Z" stroke="currentColor" strokeWidth="2" fill="currentColor" fillOpacity="0.1" />
                                                                <path d="M20 75V60L50 70V85L20 75Z" stroke="currentColor" strokeWidth="1.5" />
                                                                <path d="M15 60H11M15 75H11M13 60V75" stroke="currentColor" strokeWidth="1" strokeDasharray="1 1" />
                                                            </svg>
                                                        )}
                                                    </div>
                                                )}
                                            </div>
                                            <span className="block text-xl font-bold text-gray-900 dark:text-white group-hover:text-primary transition-colors">{val}</span>
                                            <span className="block text-[9px] uppercase tracking-widest text-gray-400 mt-1 font-semibold">{labels[key]}</span>
                                        </div>
                                    );
                                })}
                            </div>
                        </div>
                    )}

                    {/* ‚îÄ‚îÄ Variants / Sizes & Prices ‚îÄ‚îÄ */}
                    <div className="mb-8">
                        <h3 className="text-[10px] uppercase tracking-[0.25em] font-bold mb-6 text-gray-400">
                            Dimensions &amp; Prix
                        </h3>
                        <div className={`grid grid-cols-1 ${product.variantsImage ? "md:grid-cols-[1fr_1.2fr] gap-8" : ""} items-start`}>
                            {product.variantsImage && (
                                <div className="w-full aspect-square bg-gradient-to-br from-gray-50 to-gray-100 dark:from-gray-800 dark:to-gray-900 rounded-2xl flex items-center justify-center p-6 border border-gray-100 dark:border-gray-700/50 overflow-hidden group">
                                    <img
                                        src={product.variantsImage}
                                        alt="Guide des dimensions"
                                        className="max-w-full max-h-full object-contain group-hover:scale-110 transition-transform duration-700"
                                    />
                                </div>
                            )}

                            <div className="space-y-2">
                                {product.variants.map((v, i) => (
                                    <button
                                        key={i}
                                        onClick={() => setSelectedVariant(i)}
                                        className={`w-full flex justify-between items-center px-5 py-4 rounded-2xl border-2 transition-all duration-300
                                            ${selectedVariant === i
                                                ? "border-primary bg-primary/5 dark:bg-primary/10 shadow-sm"
                                                : "border-gray-100 dark:border-gray-800/60 hover:border-gray-300 dark:hover:border-gray-600 bg-transparent"
                                            }`}
                                    >
                                        <div className="flex items-center gap-3">
                                            <div className={`w-4 h-4 rounded-full border-2 flex items-center justify-center transition-colors
                                                ${selectedVariant === i ? "border-primary" : "border-gray-300 dark:border-gray-600"}`}>
                                                {selectedVariant === i && <div className="w-2 h-2 rounded-full bg-primary" />}
                                            </div>
                                            <span className={`text-sm font-semibold ${selectedVariant === i ? "text-gray-900 dark:text-white" : "text-gray-600 dark:text-gray-400"}`}>
                                                {v.size}
                                            </span>
                                        </div>
                                        <div className="flex gap-3 items-baseline">
                                            {v.promoPrice && v.promoPrice < v.price && (
                                                <span className="line-through text-gray-400 text-xs">
                                                    ‚Ç¨ {v.price.toLocaleString("fr-FR", { minimumFractionDigits: 2 })}
                                                </span>
                                            )}
                                            <span className={`font-bold text-base ${v.promoPrice && v.promoPrice < v.price ? "text-red-500" : "text-gray-900 dark:text-white"}`}>
                                                ‚Ç¨ {(v.promoPrice && v.promoPrice < v.price ? v.promoPrice : v.price).toLocaleString("fr-FR", { minimumFractionDigits: 2 })}
                                            </span>
                                        </div>
                                    </button>
                                ))}
                            </div>
                        </div>
                    </div>

                    {/* ‚îÄ‚îÄ Accessories ‚îÄ‚îÄ */}
                    {product.accessories && product.accessories.length > 0 && (
                        <div className="mb-8">
                            <h3 className="text-[10px] uppercase tracking-[0.25em] font-bold mb-4 text-gray-400">
                                Accessoires
                            </h3>
                            <div className="space-y-3">
                                {product.accessories.map((acc, i) => (
                                    <div key={i} className="flex justify-between items-center p-4 bg-gray-50 dark:bg-gray-800/40 rounded-2xl border border-gray-100 dark:border-gray-800/50 hover:border-primary/20 transition-colors">
                                        <div className="flex items-center gap-4">
                                            {acc.image && (
                                                <div className="w-12 h-12 rounded-xl overflow-hidden bg-white dark:bg-gray-700/30 shrink-0 p-1">
                                                    <img src={acc.image} alt="" className="w-full h-full object-contain" />
                                                </div>
                                            )}
                                            <span className="text-sm font-semibold text-gray-800 dark:text-gray-200">{acc.name[lang] || acc.name.en}</span>
                                        </div>
                                        <div className="flex gap-3 items-baseline">
                                            {acc.promoPrice && acc.promoPrice < acc.price && (
                                                <span className="line-through text-gray-400 text-xs">‚Ç¨ {acc.price.toLocaleString("fr-FR", { minimumFractionDigits: 2 })}</span>
                                            )}
                                            <span className={`font-bold text-sm ${acc.promoPrice && acc.promoPrice < acc.price ? "text-red-500" : "text-gray-900 dark:text-white"}`}>
                                                ‚Ç¨ {(acc.promoPrice && acc.promoPrice < acc.price ? acc.promoPrice : acc.price).toLocaleString("fr-FR", { minimumFractionDigits: 2 })}
                                            </span>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>
                    )}

                    {/* ‚îÄ‚îÄ CTA Buttons ‚îÄ‚îÄ */}
                    <div className="flex flex-col gap-3 mb-10">
                        <button className="w-full bg-primary text-white py-4.5 px-8 text-xs uppercase tracking-[0.2em] font-bold rounded-2xl flex items-center justify-center gap-3 hover:shadow-xl hover:shadow-primary/20 hover:-translate-y-0.5 transition-all duration-300 active:scale-[0.98]">
                            Demander des Infos
                            <ArrowRight size={16} />
                        </button>
                        <Link
                            href="/showroom"
                            className="w-full text-center border-2 border-gray-200 dark:border-gray-700 text-gray-700 dark:text-gray-300 py-4.5 px-8 text-xs uppercase tracking-[0.2em] font-bold rounded-2xl hover:border-primary hover:text-primary transition-all duration-300 block"
                        >
                            Visiter le Showroom
                        </Link>
                    </div>

                    {/* ‚îÄ‚îÄ Trust Badges ‚îÄ‚îÄ */}
                    <div className="grid grid-cols-3 gap-4 pt-8 border-t border-gray-100 dark:border-gray-800/40">
                        {[
                            { icon: Truck, label: "Livraison\nGratuite" },
                            { icon: ShieldCheck, label: "Garantie\n2 Ans" },
                            { icon: Phone, label: "Support\n24/7" },
                        ].map(({ icon: Icon, label }, i) => (
                            <div key={i} className="flex flex-col items-center text-center gap-2">
                                <Icon size={20} className="text-primary" strokeWidth={1.5} />
                                <span className="text-[10px] text-gray-400 font-medium leading-tight whitespace-pre-line">{label}</span>
                            </div>
                        ))}
                    </div>
                </div>
            </section>
        </motion.div>
    );
}
